.card.dark{background-color:var(--dark);color:var(--white)}.card.light{background-color:var(--white);color:var(--on-surface)}.topSection{text-align:center}.assetBlock{display:flex;justify-content:space-between;align-items:center;padding:1rem;border-radius:8px;box-shadow:0 2px 6px rgba(0,0,0,.1);transition:transform .3s ease-in-out}.assetBlock.dark{background-color:var(--dark);color:var(--white)}.assetBlock.light{background-color:var(--white);color:var(--on-surface)}.assetBlock:hover{transform:translateY(-3px)}.assetBlock .left{display:flex;flex-direction:column;justify-content:flex-start}.assetBlock .left .balanceTitle{font-size:1rem;font-weight:500}.assetBlock .left .assetTitle{font-size:1.25rem;font-weight:bold}.assetBlock .left .assetDetails{display:flex;align-items:center;margin-top:.5rem}.assetBlock .left .assetDetails .logo{width:40px;height:40px;border-radius:50%;margin-right:.5rem}.assetBlock .left .assetDetails .assetInfo{display:flex;flex-direction:column}.assetBlock .left .assetDetails .assetInfo .symbol{font-weight:bold}.assetBlock .left .assetDetails .assetInfo .marketCap{font-size:.9rem;color:#757575}.assetBlock .right{display:flex;flex-direction:column;justify-content:center;align-items:flex-end}.assetBlock .right .priceBlock{margin-bottom:.5rem}.assetBlock .right .priceBlock .value{font-size:14px;font-weight:bold}.negative{color:var(--critical)}.positive{color:var(--success)}.priceContainer{display:flex;justify-content:space-between;align-items:center;margin-top:1rem}.priceContainer .price{font-size:1.5rem;font-weight:bold;color:var(--secondary)}.priceContainer .symbol{font-size:1rem;color:var(--secondary)}.loading{font-size:1.5rem;color:var(--secondary);text-align:center}.topUpBtn{border-radius:100px}.topUpBtn.light{background-color:var(--white) !important;color:var(--on-surface);border:1px solid var(--on-surface)}.topUpBtn.dark{background-color:var(--dark);color:var(--surface);border:1px solid var(--line)}